<template>
    <v-app>
        <v-main>
            <v-container>

                <v-icon>mdi-account-plus</v-icon>
                <p>社員情報登録</p>
                <p>社員情報入力画面</p>

                <v-text-field label="社員ID" clearable v-model="inputEmpId" @input="checkEmpId" counter="4"
                              hint=" 社員IDをA001～Z999の間の半角英数字で入力して下さい。
                              *社員IDは大文字のアルファベット＋三桁の数字の組み合わせです"
                              :rules="[id_Max_Char]"></v-text-field>
                <p v-if="idError" class="error-message">有効な社員IDを入力してください。</p>

                <v-text-field label="社員名" clearable v-model="inputEmpName" @input="checkEmpName" counter="255"
                              hint="社員名を255文字以内の全角文字で入力して下さい"
                              :rules="[name_Max_Char]"></v-text-field>
                <p v-if="nameError" class="error-message">有効な社員名を入力してください。</p>

            </v-container>

            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="RETURN">戻る</v-btn>
                </v-col>
                <v-col cols="auto">
                    <v-spacer></v-spacer> 
                </v-col>
                <v-col cols="auto">
                    <v-btn v-on:click="INSERT" :disabled="nameError || idError ||
                           !inputEmpId || !inputEmpName">登録</v-btn>
                </v-col>
            </v-row>


        </v-main>
    </v-app>
</template>


<style type="text/css">
    p {
        font-family: 'Century', Times, serif;
        color:black;
    }
    .error-message {
        color: red;
    }
</style>

<script>
    export default {
        data() {
            return {
                inputEmpId: '',
                idError: false,
                inputEmpName: '', 
                nameError: false,
            };
        },
        methods: {
            id_Max_Char(value) {
                if (value.length <= 4) {
                    return true;
                } else {
                    return '社員IDは4文字以下で入力してください';
                }
            },
            name_Max_Char(value) {
                if (value.length <= 255) {
                    return true; 
                } else {
                    return '社員名は255文字以下で入力してください'; 
                }
            },
            checkEmpId() {
                if (/[A-Z]{1}[0-9]{3}/.test(this.inputEmpId) && !/^(?!.*000).+$/.test(this.inputEmpId)) {
                    this.idError = true;
                } else {
                    this.idError = false;
                }
            },
            checkEmpName() {
                if (!/^[一-龯ぁ-んァ-ヶー]*$/.test(this.inputEmpName)) {
                    this.nameError = true;
                } else {
                    this.nameError = false;
                }
            },

            INSERT() {
               this.$router.push({ name: 'ins_Confi', params: { empId: this.inputEmpId, empName: this.inputEmpName } });
            },
            RETURN() {
                this.$router.push({ name: 'sel' });
            },
        }
    }

</script>