<template>
    <v-app>
        <v-main>
            <v-container>
                <v-icon>mdi-account-search</v-icon>
                <p>社員情報検索</p>
                <p>入力画面</p>

                <v-text-field label="検索バー" clearable v-model="input_Emp_Search" @input="check_Emp_Search" counter="255"
                              hint="検索したい文字を255文字以内で入力して下さい"
                              :rules="[id_Max_Char]"></v-text-field>
                <p>検索する文字は半角文字のみか全角文字のみのどちらかで入力して下さい</p>
                <p v-if="char_Error" class="error-message">文字数を255文字以内にしてください。</p>

                <v-row>

                    <v-col cols="3">
                        <v-radio-group label="優先順位" v-model="search_Prime">
                            <v-radio label="社員ID" value="syainID"></v-radio>
                            <v-radio label="社員名" value="syainNAME"></v-radio>
                        </v-radio-group>
                    </v-col>

                    <v-col cols="3">
                        <v-radio-group label="検索条件" v-model="search_Option">
                            <v-radio label="前方一致" value="front"></v-radio>
                            <v-radio label="後方一致" value="back"></v-radio>
                            <v-radio label="部分一致" value="part"></v-radio>
                            <v-radio label="全て一致" value="all"></v-radio>
                        </v-radio-group>
                    </v-col>


                    <v-col cols="3">
                        <v-radio-group label="社員ID表示順" v-model="id_Order">
                            <v-radio label="昇順" value="ASC"></v-radio>
                            <v-radio label="降順" value="DESC"></v-radio>
                        </v-radio-group>
                    </v-col>

                    <v-col cols="3">
                        <v-radio-group label="社員名表示順" v-model="name_Order">
                            <v-radio label="昇順" value="ASC"></v-radio>
                            <v-radio label="降順" value="DESC"></v-radio>
                        </v-radio-group>
                    </v-col>

                </v-row>
            </v-container>



            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="RETURN">戻る</v-btn>
                </v-col>
                <v-col cols="auto">
                    <v-spacer></v-spacer>
                </v-col>
                <v-col cols="auto">
                    <v-btn v-on:click="SEARCH" :disabled="!input_Emp_Search">検索</v-btn>
                </v-col>
            </v-row>


        </v-main>
    </v-app>
</template>


<style type="text/css">
    p {
        font-family: 'Century', Times, serif;
        color:black;
    }
    .error-message {
        color: red;
    }
</style>

<script>
    export default {
        data() {
            return {
                search_Prime: 'syainID',
                search_Option: 'front',
                id_Order: 'ASC',
                name_Order: 'ASC',
                input_Emp_Search: '', 
                search_Target: '',
                char_Error: true,
            };
        },
        methods: {
            id_Max_Char(value) {
                if (value.length >= 255) {
                    this.char_Error = true;
                } else {
                    this.char_Error = false;
                }
                return true;
            },
            check_Emp_Search() {
                if (/^[ -~]/.test(this.input_Emp_Search)) {
                    this.search_Target = 'ID'; 
                } else {
                    this.search_Target = 'NAME'; 
                }
                return true;
            },

            SEARCH() {
                this.$router.push({
                    name: 'sele_Result', params: {
                        search_Prime: this.search_Prime, search_Option: this.search_Option,
                        id_Order: this.id_Order, name_Order: this.name_Order,
                        input_Emp_Search: this.input_Emp_Search, search_Target: this.search_Target
                    }
                });
            },
            RETURN() {
                this.$router.push({ name: 'sel' });
            },
        }
    }

</script>