<template>
    <v-app>
        <v-main>
            <v-container>
                <v-icon>mdi-account-search</v-icon>
                <p>社員情報検索</p>
                <p>表示画面</p>


                <template>
                    <v-container fluid>
                        <v-row justify="center">
                            <v-col cols="3">
                                <table class="table-style">
                                    <thead>
                                        <tr>
                                            <th>ID</th>
                                            <th>名前</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(employee, index) in response_Data" :key="index">
                                            <td>{{ employee.syainID }}</td>
                                            <td>{{ employee.syainNAME }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </v-col>
                        </v-row>
                    </v-container>
                </template>

            </v-container>


            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="RETURN">戻る</v-btn>
                </v-col>
            </v-row>


        </v-main>
    </v-app>
</template>


<style type="text/css">
    p {
        font-family: 'Century', Times, serif;
        color:black;
    }
    .error-message {
        color: red;
    }
    .table-style {
        width: 75%;
        border-collapse: collapse;
    }
        .table-style th {
            padding: 8px;
            text-align: center;
            border: 5px solid #4e4e4e;
        }
        .table-style td {
            padding: 8px;
            text-align: center;
            border: 1px solid #4e4e4e;
        }
</style>

<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                search_Prime: '',
                search_Option: '',
                id_Order: '',
                name_Order: '',
                input_Emp_Search: '', 
                search_Target: '',
                response_Data: [],
            };
        },
        created() {
            // ルーターからパラメータを取得してデータに代入
            this.search_Prime = this.$route.params.search_Prime;
            this.search_Option = this.$route.params.search_Option;
            this.id_Order = this.$route.params.id_Order;
            this.name_Order = this.$route.params.name_Order;
            this.input_Emp_Search = this.$route.params.input_Emp_Search;
            this.search_Target = this.$route.params.search_Target;
        },
        mounted() {
            axios
                .request({
                    method: 'POST',
                    url: 'http://localhost:8080/select_Func',
                    data: {
                        search_Prime: this.search_Prime,
                        search_Option: this.search_Option,
                        id_Order: this.id_Order,
                        name_Order: this.name_Order,
                        nameOver: this.empName,
                        input_Emp_Search: this.input_Emp_Search,
                        search_Target: this.search_Target
                    }
                })
                .then((response) => {
                    this.response_Data = response.data.map((item) => {
                        const parts = item.split(", ");
                        const syainID = parts[0].split("=")[1];
                        const syainNAME = parts[1].split("=")[1];

                        return {
                            syainID,
                            syainNAME,
                        };
                    });

                })
        },
        methods: {

            RETURN() {
                this.$router.push({ name: 'sele_Inp' });
            },
        }
    }

</script>